import { ComposerType } from "@/model/ComposerType";
import { Module } from "vuex";
import { RootState } from "..";
import { loadObjFromLocal, withSaveStringLocal } from "../utils";

export interface UI {
    composerType: ComposerType,
    composerHeight: number
}

export const uiModule: Module<UI, RootState> = {
    state: {
        composerHeight: 80,
        composerType: loadObjFromLocal<ComposerType>('composerType', ComposerType.STANDARD),
    },
    mutations: {
        updateComposerHeight(state, height) {
            state.composerHeight = height;
        },
        updateComposerType: withSaveStringLocal('composerType', (state, type: ComposerType) => {
            state.composerType = type;
        }),
    },
}

export const
    UPDATE_COMPOSER_TYPE = 'updateComposerType',
    UPDATE_COMPOSER_HEIGHT = 'updateComposerHeight';