import { Position } from "./position";

export class PositionArea {

    readonly center: Position;
    readonly radius: number;

    constructor({ center, radius }: { center: Position; radius: number; }) {
        this.center = center;
        this.radius = radius;
    }

    includePosition(pos: Position): boolean {
        //const performanceStart = performance.now();

        //TODO - distanceWithVincentyInverse viene chiamata due volte (una volta qui e una volta per stampare nel radar)! Ottimizzare!
        const vincentyInverse = this.center.distanceWithVincentyInverse(pos);
        const centerPointDist = vincentyInverse.distance;

        /* const performanceEnd = performance.now();
        console.log(`distanceVincentyInverse exec time: ${performanceEnd - performanceStart}`); */

        if (centerPointDist < this.radius) {
            return true;
        }
        return false;
    }
}