import { NigmaMessage } from "@/model/Message";
import { Module } from "vuex";
import { RootState } from "..";
import {  loadObjFromLocal, loadStringFromLocal, withSaveObjLocal, withSaveStringLocal } from "../utils";

export interface Session {
    messageHistory: NigmaMessage[],
    sessionId: string,
    position: GeolocationPosition | null
}

//const endpointUrl = 'https://i9y86y0v1l.execute-api.eu-west-2.amazonaws.com/prod';
// const endpointUrl = 'http://localhost:3000';
const endpointUrl = 'http://192.168.1.111:3000';

export const sessionModule: Module<Session, RootState> = {
    state: {
        messageHistory: loadObjFromLocal<NigmaMessage[]>('messageHistory', []),
        sessionId: loadStringFromLocal("sessionId", ''),
        position: null,
    },
    mutations: {
        addMessage: withSaveObjLocal<Session>('messageHistory', (state, message: NigmaMessage) => {
            state.messageHistory = [...state.messageHistory, message];
        }),
        sendMessageReq() {
            // console.log(request);
        },
        sendMessageRes: withSaveObjLocal<Session>('messageHistory', (state, resMessages: NigmaMessage[]) => {
            //console.log(resMessages);
            state.messageHistory = [...state.messageHistory, ...resMessages];
        }),
        sendMessageErr(state, error) {
            console.error(error);
            //
        },
        createSessionReq(state, request) {
            // console.log(request);
            //
        },
        createSessionRes: withSaveStringLocal('sessionId', (state, sessionId) => {
            state.sessionId = sessionId;
        }),
        createSessionErr(state, error) {
            console.error(error);
            //
        },
        resetMessages: withSaveObjLocal('messageHistory', (state) => {
            state.messageHistory = [];
        }),
        updatePosition(state, position) {
            state.position = position;
        },
    },
    actions: {
        async createSession({ commit, state, dispatch }, sessionId?) {
            const req: RequestInit = {
                method: 'POST',
                redirect: 'follow'
            };
            if (sessionId) {
                req.body = JSON.stringify({ sessionId });
            }
            commit(CREATE_SESSION_REQ, req);
            try {
                const fRes = await fetch(`${endpointUrl}/createSession`, req);
                const res = await fRes.json();
                const sessionId = res.sessionId;
                commit(CREATE_SESSION_RES, sessionId);
                const resMessages = res.messages.map(
                    (msg: NigmaMessage) => (new NigmaMessage({ text: msg.text, sender: 'other', image: msg.image, delay: msg.delay, customKeyboard: msg.customKeyboard, options: msg.options, triggerArea: msg.triggerArea })));
                commit(SEND_MESSAGE_RES, resMessages);
            } catch (error) {
                commit(CREATE_SESSION_ERR, error);
            }
        },
        async sendMessage({ commit, state, dispatch }, message) {
            if (!state.sessionId) {
                throw new Error("sessionId null!");
            }
            commit(ADD_MESSAGE, { text: message, sender: 'me' });
            const req: RequestInit = {
                method: 'POST',
                body: JSON.stringify({
                    sessionId: state.sessionId,
                    message,
                }),
                redirect: 'follow'
            };
            commit(SEND_MESSAGE_REQ, req);
            try {
                const res = await (await fetch(`${endpointUrl}/elaborateMessage`, req)).json();
                const resMessages = res.messages.map(
                    (msg: NigmaMessage) => (new NigmaMessage({ text: msg.text, sender: 'other', image: msg.image, delay: msg.delay, customKeyboard: msg.customKeyboard, options: msg.options, triggerArea: msg.triggerArea })));
                commit(SEND_MESSAGE_RES, resMessages);
            } catch (error) {
                commit(SEND_MESSAGE_ERR, error);
            }

        },
        saveStringOnLocal(state, { strKey, str }) {
            localStorage.setItem(strKey, str);
        },
        resetSession({ commit, state, dispatch }) {
            commit(RESET_MESSAGES);
            dispatch('createSession', state.sessionId);
        }
    }
}

export const ADD_MESSAGE = 'addMessage',
    SEND_MESSAGE_REQ = 'sendMessageReq',
    SEND_MESSAGE_RES = 'sendMessageRes',
    SEND_MESSAGE_ERR = 'sendMessageErr',
    CREATE_SESSION_REQ = 'createSessionReq',
    CREATE_SESSION_RES = 'createSessionRes',
    CREATE_SESSION_ERR = 'createSessionErr',
    UPDATE_POSITION = 'updatePosition',
    ACTION_SEND_MESSAGE = 'sendMessage',
    RESET_MESSAGES = 'resetMessages';