
import { Position, VincentyInverseRes } from "@/model/position";
import { Options, Vue } from "vue-class-component";

@Options({
  data() {
    const destCoord: VincentyInverseRes | null = null;
    return { destCoord };
  },
  computed: {
    targetAngleStyle() {
      if (this.destCoord) {
        const typedDest: VincentyInverseRes = this.destCoord;
        const averageAngle =
          (typedDest.initialBearing + typedDest.finalBearing) / 2;
        return `transform: rotate(${averageAngle}deg)`;
      }
      return `transform: rotate(0deg)`;
    },
    targetPositionStyle() {
      if (this.destCoord) {
        const typedDest: VincentyInverseRes = this.destCoord;
        console.log(typedDest.distance);
        const maxD = 1000;
        const normalizedDist = (typedDest.distance / maxD) * 50;
        return `top: ${50 - normalizedDist}%;`;
      }
      return `top: ${-20}%;`;
    },
  },
  watch: {
    "$store.state.session.position"(newP /* : GeolocationPosition */) {
      const store = this.$store; //useStore(key);
      const center = new Position(newP.coords);
      const msgs = store.state.session.messageHistory;
      const triggerArea = msgs[msgs.length - 1].triggerArea;
      if (triggerArea) {
        // console.log("triggerArea ", triggerArea);
        const dest: Position = triggerArea.center;
        this.destCoord = center.distanceWithVincentyInverse(dest);
        // console.log('destCoord ', this.destCoord);
      }
    },
  },
})
export default class Radar extends Vue {}
