import { Module } from "vuex";
import { RootState } from "..";
import { loadObjFromLocal, loadStringFromLocal, withSaveObjLocal, withSaveStringLocal } from "../utils";

export interface Settings {
    fastMode: boolean,
    font: 'Helvetica' | 'Aquire' | string,
    geopositionEnabled: boolean
}

export const settingsModule: Module<Settings, RootState> = {
    state: {
        fastMode: loadObjFromLocal<boolean>('fastMode', false),
        font: loadStringFromLocal("font", 'Aquire'),
        geopositionEnabled: loadObjFromLocal<boolean>("geopositionEnabled", false)
    },
    mutations: {
        toggleFastMode: withSaveObjLocal('fastMode', (state, on) => {
            if (on != null) {
                state.fastMode = on;
            } else {
                state.fastMode = !state.fastMode;
            }
        }),
        toggleFont: withSaveStringLocal('font', (state, font) => {
            if (font != null) {
                state.font = font;
            } else {
                state.font = state.font === 'Avenir' ? 'Aquire' : 'Avenir';
            }
        }),
        toggleGeoposition: withSaveObjLocal('geopositionEnabled', (state, enable) => {
            if (enable != null) {
                state.geopositionEnabled = enable;
            } else {
                state.geopositionEnabled = !state.geopositionEnabled;
            }
        }),
    }
}

export const TOGGLE_FAST_MODE = 'toggleFastMode', TOGGLE_FONT = 'toggleFont', TOGGLE_GEOPOSITION = 'toggleGeoposition'