
import { NigmaMessage } from "@/model/Message";
import { ComposerType } from "@/model/ComposerType";
import { UPDATE_COMPOSER_HEIGHT } from "@/store/modules/ui";
import { Options, Vue } from "vue-class-component";
import HintButton from "./HintButton.vue";
import CustomKeyboard from "./CustomKeyboard.vue";
import Radar from "./Radar.vue";
import { ACTION_SEND_MESSAGE, UPDATE_POSITION } from "@/store/modules/session";
import { PositionArea } from "@/model/position-area";
import { useStore } from "vuex";
import { key } from "@/store";
import { Position } from "@/model/position";

@Options({
  components: {
    HintButton,
    CustomKeyboard,
    Radar
  },
  data() {
    return {
      msg: "",
      ComposerType,
      watchId: -1
    };
  },
  computed: {
    options() {
      const messages: NigmaMessage[] = this.$store.state.session.messageHistory;
      return messages.length > 0 ? messages[messages.length - 1].options : null;
    },
    customKeyboard() {
      const messages: NigmaMessage[] = this.$store.state.session.messageHistory;
      return messages.length > 0
        ? messages[messages.length - 1].customKeyboard
        : null;
    },
    composerType() {
      return this.$store.state.ui.composerType;
    },
  },
  methods: {
    updateMsg(e: any) {
      this.msg = e.target!.innerText;
    },
    sendMessage(message: string) {
      const trimmedMsg = message.trim();
      if (trimmedMsg.length > 0) {
        this.msg = "";
        this.$store.dispatch("sendMessage", trimmedMsg);
      }
    },
    startWatchPosition() {
      if ("geolocation" in navigator) {
        const store = useStore(key);
        //TODO - Brutto, da spostare in Vuex
        const msgs = store.state.session.messageHistory;
        const lastMessage = msgs[msgs.length - 1];
        const center = new Position({...lastMessage.triggerArea!.center});
        const destArea = new PositionArea({center, radius: lastMessage.triggerArea!.radius});
        this.watchId = navigator.geolocation.watchPosition(
          (pos) => {
            this.$store.commit(UPDATE_POSITION, pos);
            if (destArea.includePosition(new Position(pos.coords))) {
              store.dispatch(
                ACTION_SEND_MESSAGE,
                JSON.stringify({
                  latitude: pos.coords.latitude,
                  longitude: pos.coords.longitude,
                })
              );
              navigator.geolocation.clearWatch(this.watchId);
            }
          },
          (err) => {
            console.error(err);
            navigator.geolocation.clearWatch(this.watchId);
          },
          {
            enableHighAccuracy: true,
            // timeout: 5000,
            maximumAge: 0,
          }
        );
      }
    },
  },
  updated() {
    this.$store.commit(UPDATE_COMPOSER_HEIGHT, this.$el.offsetHeight);
    if (this.composerType === ComposerType.POSITION) {
      this.startWatchPosition();
    }
  },
  mounted() {
    this.$store.commit(UPDATE_COMPOSER_HEIGHT, this.$el.offsetHeight);
    if (this.composerType === ComposerType.POSITION) {
      this.startWatchPosition();
    }
  },
  beforeUnmount() {
    navigator.geolocation.clearWatch(this.watchId);
  }
})
export default class MessageComposer extends Vue {}
