
import { Vue, Options } from "vue-class-component";
import {
  TOGGLE_FAST_MODE,
  TOGGLE_FONT,
  TOGGLE_GEOPOSITION,
} from "@/store/modules/settings";
import {
  UPDATE_POSITION,
} from "@/store/modules/session";

@Options({
  data() {
    const showMenu = false;
    return { showMenu };
  },
  methods: {
    onClickOutside(e: MouseEvent, el: any) {
      this.showMenu = false;
    },
    resetSession() {
      this.$store.dispatch("resetSession");
      this.showMenu = false;
    },
    toggleFastMode() {
      this.$store.commit(TOGGLE_FAST_MODE);
      // this.showMenu = false;
    },
    toggleFont() {
      this.$store.commit(TOGGLE_FONT);
    },
    toggleGeo() {
      this.$store.commit(TOGGLE_GEOPOSITION);
      /* if (this.geoOn) {
        this.startWatchPosition();
      } */
    },
    
  },
  computed: {
    fastMode() {
      return this.$store.state.settings.fastMode;
    },
    readableFont() {
      return this.$store.state.settings.font === "Avenir";
    },
    geoOn() {
      return this.$store.state.settings.geopositionEnabled;
    },
    pos() {
      return this.$store.state.session.position;
    },
  }
})
export default class MoreMenu extends Vue {}
