
import { NigmaMessage } from "@/model/Message";
import { key } from "@/store";
import { UPDATE_COMPOSER_TYPE } from "@/store/modules/ui";
import { nextTick } from "@vue/runtime-core";
import { Options, Vue } from "vue-class-component";
import { useStore } from "vuex";
import MessageWrapper from "./MessageWrapper.vue";
import Loading from "./Loading.vue";
import { ComposerType } from "@/model/ComposerType";

@Options({
  components: {
    MessageWrapper,
    Loading,
  },
  watch: {
    "$store.state.ui.composerHeight"(newH) {
      this.setPaddingBottom(newH);
    },
    async "$store.state.session.messageHistory"(newM, oldM) {
      const newElements: NigmaMessage[] = newM.slice(
        -(newM.length - oldM.length)
      );
      if (newM.length === 0) {
        this.messages = [];
      }
      for (const msg of newElements) {
        // console.log(msg);
        if (msg.delay) {
          this.writing = true;
          nextTick(() => {
            this.scrollOnBottom();
          });
          await awaitMillis(msg.delay / (this.fastMode ? 5 : 1));
          this.writing = false;
          this.messages.push(msg);
        } else {
          this.messages.push(msg);
        }
        this.$store.commit(UPDATE_COMPOSER_TYPE, this.composerTypeFromMsg(msg));

        nextTick(() => {
          this.scrollOnBottom();
        });
      }
    },
  },
  computed: {
    fastMode(): boolean {
      return this.$store.state.settings.fastMode;
    },
  },
  data() {
    const messages: NigmaMessage[] = useStore(key).state.session.messageHistory;
    const writing = false;
    return { messages, writing };
  },
  methods: {
    scrollOnBottom: function () {
      const el = this.$el;
      el.children[el.childElementCount - 1].scrollIntoView({
        behavior: "smooth",
      });
    },
    setPaddingBottom: function (paddingBottom: number) {
      const el = this.$el;
      el.style.paddingBottom = paddingBottom + "px";
    },
    composerTypeFromMsg(msg: NigmaMessage): ComposerType {
      if (msg.triggerArea) {
        return ComposerType.POSITION;
      } else if (msg.customKeyboard) {
        return ComposerType.CUSTOM_KEYBOARD;
      } else if (!msg.customKeyboard && msg.options) {
        return ComposerType.MULTIPLE_CHOICE;
      } else {
        return ComposerType.STANDARD;
      }
    },
  },
})
export default class ChatHistory extends Vue {}

function awaitMillis(millis: number): Promise<void> {
  return new Promise<void>((resolve) => {
    setTimeout(() => {
      resolve();
    }, millis);
  });
}
